.about {
  width: 895px;
  align-self: center;
  margin: 5px 0 0 5px;
  font-size: 24px;
}

#A2 {
  height: auto;
}

#title-about{
  margin-bottom: 5px;
  margin-top:0;
}

#about {
  display: flex;
}

.title {
  background-color: #515A5A   ;
  color: white;
  font-size: 24px;
  margin: 5px 0 0 0;
  width: 90%;
  padding: 0 5% 0 5%;
}

#me {
  width: 100%;
  height: auto;
}

#life {
  width: 90%;
  padding: 0 5% 0 5%;
  background-color: #839192 ;
  color: white;
}

#skills {
  width: 90%;
  background-color: #839192;
  color: white;
  padding: 0 5% 0 5%;
}

@media only screen and (max-width: 900px) {
  .about {
    width: 95vw;
    margin-left: 0;
    margin-right: 0;
    height: auto;
  }
}

@media only screen and (max-width: 500px) {
  #name {
    font-size: 20px;
  }

  #me {
    height: 100%;
  }
}
